*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

* a{
    text-decoration: none;
    color: black;
}

nav > a{
    text-decoration: none;
}

header{
    background-color: #FFF7ED;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 20px;
}

.site-logo{
font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
font-weight: 900;
font-size: 25.35px;
line-height: 40.27px;
letter-spacing: 0%;
text-decoration: none;
color: black;
}


.home-container{
